import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Database } from '../../providers/database';

@IonicPage()
@Component({
  selector: 'page-favorite',
  templateUrl: 'favorite.html',
})
export class Favorite {
  recipies: any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public db: Database) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Favorite');
    this.recipies = this.db.getFavoriteRecipies();
  }

  likeToggle(recipe: any) {
    recipe.liked = !recipe.liked;
  }

  direction(recipe) {
    this.navCtrl.push('Direction', {recipe: recipe});
  }

  add() {
    this.navCtrl.push('NewRecipe');
  }

  /**
   * Map & Search
   */
  map() {
    this.navCtrl.setRoot('Map');
  }
  search() {
    this.navCtrl.setRoot('Search');
  }
}



